body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0B2434;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  background-color: #F5F5F5;
  height: 450px;
  width: 450px;
  max-width: 800px;
  border-radius: 10px;
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  padding: 20px;
}

.title {
  font-size: 30px;
}

.dices-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  grid-gap: 20px;
  margin: 40px 0px;
}

.roll-button {
  background-color: rgb(115, 6, 92);
  color: white;
  font-size: 1.2rem;
  font-weight: 800;
  border-radius: 10px;
  padding: 10px 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  align-self: center;
  border: none;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
}

.roll-button:active {
  background-color: rgb(126, 43, 151);
}

.roll-button:focus {
  outline: none;
}

.rolls {
  color: red;
  font-weight: 600;
}

.hidden {
  visibility: hidden;
}

.single-dice {
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  background-color: white;
  border-radius: 10px;
  height: 50px;
  width: 50px;
  cursor: pointer;
}

.dot {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: black;
  flex-shrink: 0;
}

.first-face {
  height: 80%;
  width: 80%;
  margin: 10% auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.second-face {
  height: 80%;
  width: 80%;
  margin: 10% auto;
  display: flex;
  justify-content: space-between;
}

.second-face .dot:nth-last-of-type(2) {
  align-self: flex-end;
}

.third-face {
  height: 80%;
  width: 80%;
  margin: 10% auto;
  display: flex;
  justify-content: space-between;
}

.third-face .dot:nth-last-of-type(2) {
  align-self: center;
}

.third-face .dot:nth-last-of-type(3) {
  align-self: flex-end;
}

.fourth-face {
  height: 80%;
  width: 80%;
  margin: 10% auto;
  display: flex;
  justify-content: space-between;
}

.fourth-face .column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.fifth-face {
  height: 80%;
  width: 80%;
  margin: 10% auto;
  display: flex;
  justify-content: space-between;
}

.fifth-face .column:nth-of-type(1) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.fifth-face .column:nth-of-type(2) {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.fifth-face .column:nth-of-type(3) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

